import React from 'react';
import CircularProgress from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';

const CircularProgressBar = ({ percentage }) => {
  return (
    <>
    <Box position="relative" display="inline-flex" alignItems="center" height="100%">
      <CircularProgress
        variant="determinate"
        value={percentage}
        size={250} // Adjust the size as needed
        thickness={4} // Adjust the thickness as needed
        sx={{ color: '#C7DFF7' }} // Change color to the desired color
        
      />
      <Box
        position="absolute"
        display="flex"
        alignItems="center"
        justifyContent="center"
        width="100%"
        height="100%"
        top={0}
        left={0}
        sx={{ color: 'white' }} // Change text color to white
      >
        <Typography variant="h6" fontWeight="bold">
          {`${Math.round(percentage)}%`}
        </Typography>
      </Box>
    </Box>

    </>
  );
};

export default CircularProgressBar;
